import React, { useContext, useEffect, useRef, useState } from 'react'
import Chart from './Chart'
import List from './List'
import Insert from './Insert'
import { MyContext } from '../MyContext'


//   +10 +1 +5 
//     ?
//  , 
//  ,  


export const Home = () => {
  const [popup, setPopup] = useState(false);
  const [sortBtn, setSortBtn] = useState(false);

  const [availableMonths, setAvailableMonths] = useState([]);

  const {data, setCurrentDate, type, setType , dataList, setDataList} = useContext(MyContext);

  let totalIn=0, totalOut=0;

  //   
  let dateFilter = {y:[], m:[]};

  const body = document.querySelector("body")
  const monthSelect = document.getElementById("month")

  document.addEventListener("DOMContentLoaded", () => {
    // monthSelect.value('All')
  })
  
  if(popup) {
    body.style.cssText = "overflow:hidden;"
  } else {
    body.style.cssText = "overflow:auto;"
  }

  data.forEach((v,i)=>{
    if (v.type === '') {
      totalIn += v.amount
    } else if (v.type === '') {
      totalOut += v.amount
    }

    let dateArr = v.date.split('.');
    dateFilter.y.push(dateArr[0]);
    dateFilter.m.push(dateArr[1]);
  })
  //  sort 
  // : 2024 02   ..??
  let year = [...new Set(dateFilter.y)].sort((a,b)=>b-a)
  let totalAmount = (totalIn - totalOut);


  const yearRef = useRef();
  const monthRef = useRef();


  //  
  const handleDate = (e) => {
    let y = yearRef.current.value;
    let m = monthRef.current.value;

    let ext = data.filter(obj=>obj.date.includes(y) && obj.date.includes(m));
    
    
    
    if(e == '' || e == ''){
      ext = ext.filter(obj => obj.type === e);
    }

    if(e == '') {
      setSortBtn('')
    } else if(e == '') {
      setSortBtn('')
    } else if(e == 'changeSelect') {
      setSortBtn('')
    }

    ext.sort((a, b) => {
      const dateA = new Date(a.date.split('.').join('-'));
      const dateB = new Date(b.date.split('.').join('-'));
      return dateB - dateA;
    });
    
    setCurrentDate(y)
    setDataList(ext); 
  }

  const handleYearChange = (selectedYear) => {
    const filteredData = data.filter(item => item.date.includes(selectedYear));
    const uniqueMonths = [...new Set(filteredData.map(item => item.date.split('.')[1]))].sort((a,b)=>b-a);
    setAvailableMonths(uniqueMonths);
  };
 
  useEffect(()=>{
    handleDate('');
    handleYearChange('2024')
  },[])
  

  
  return (
    <div className='home'>
      <div className={`dim ${popup ? 'on' : ''}`}></div>
      <div>
        <div className='txt-wrap'>
          <p> </p>
          <p className='total'>{totalAmount}<span></span></p>
        </div>
        <div className='view-wrap'>
          <div className="select-wrap">
            <div className='selects'>
              <select name="year" id="year" ref={yearRef} onChange={(e)=>{handleDate('changeSelect'); handleYearChange(e.target.value);}}>
                  {
                    year.map((v,i)=>(
                      <option key={i} value={v}>{v}</option>
                    ))
                  }
              </select>
              <select name="month" id="month" ref={monthRef} onChange={()=>{handleDate('changeSelect')}}>
                <option className="default" selected value={''}>All</option>
                {availableMonths.map((v, i) => (
                  <option key={i} value={v}>{v}</option>
                ))}
              </select>
            </div>
            <div className='sort-btn'>
              <button type='button' className={`in-btn ${sortBtn === '' ? 'on' : ''}`} onClick={()=>{handleDate('')}}></button>
              <button type='button' className={`out-btn ${sortBtn === '' ? 'on' : ''}`} onClick={()=>{handleDate('')}}></button>
            </div>
          </div>
          <div className='in-out-wrap'>
            <ul>
              <li className='in'>
                <p></p>
                <p><span className='total'>{totalIn}</span></p>
              </li>
              <li className='out'>
                <p></p>
                <p><span className='total'>{totalOut}</span></p>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <List dataList={dataList} setDataList={setDataList}/>
      <Chart />
      <div className='btn-wrap'>
        <button type='button' className='add-btn' onClick={()=>{setPopup(true)}}> </button>
      </div>
      <Insert popup={popup} setPopup={setPopup} type={type} setType={setType}/>
    </div>
  )
}
