import React, { useContext, useEffect, useState } from 'react'
import { MyContext } from '../MyContext';
import axios from 'axios';

const Insert = ({popup, setPopup, type, setType }) => {

  const {data, dispatch } = useContext(MyContext);
  
  const [date, setDate] = useState('');
  const [amount, setAmount] = useState(0);
  const [memo, setMemo] = useState('');
  // const [type, setType] = useState('');
  
  const [newdata, setNewdata] = useState();
  
  // let info ~~~~ [newdata])  .......    .
  let info = () => {
    setNewdata({id:Date.now(), date, amount, memo, type})
  }
  
  useEffect(()=>{
    // newdata    !  ..
    if(!newdata) return;
    //                                     ,
    axios.post("http://localhost:3030/list",newdata)
    // dispatch  data  .
    .then(res=>dispatch({type: 'post', data:newdata}))
//    .
  },[newdata])

  return (
    <div className={`insert ${popup ? 'on' : ''}`}>
      <h2></h2>
      <ul>
        <li className='date'>
          <label htmlFor="date"></label>
          <input type='text' id='date' placeholder='. ' onChange={(e)=>{setDate(e.target.value)}} />
        </li>
        <li className='amount'>
          <label htmlFor="amount"></label>
          <input type='number' id='amount' placeholder='' onChange={(e)=>{setAmount(parseInt(e.target.value))}} />
        </li>
        <li className='memo'>
          <label htmlFor="memo"></label>
          <input type='text' id='memo' placeholder='' onChange={(e)=>{setMemo(e.target.value)}}/>
        </li>
        <li className='radio-wrap'>
          <input type="radio" name="in-or-out" className='radio' id="in" onChange={(e)=>{setType('')}} />
          <label htmlFor="in"><i></i></label>
          <input type="radio" name="in-or-out" className='radio' id="out" onChange={(e)=>{setType('')}} />
          <label htmlFor="out"><i></i></label>
        </li>
      </ul>
      <div className='btn-wrap'>
        <button type='button' className='add-info' onClick={()=>{info(); setPopup(false)}}></button>
        <button type='button' className='close' onClick={()=>{setPopup(false)}}></button>
      </div>
    </div>
  )
}

export default Insert